.post_recomended {
  padding: 60px 0 40px;
  position: relative;
  z-index: 1;
  @media (min-width: $screen-md) {
    padding: 80px 0 60px;
  }

  .title {
    @include pop(400, 23, 136%);
    letter-spacing: -0.46px;
    @include titleGrad();
    margin-bottom: 20px;
    text-align: left;
    @media (min-width: $screen-md) {
      font-size: 29px;
      line-height: 124%;
      letter-spacing: -0.58px;
      margin-bottom: 30px;
    }
  }

  .recomendations {
    @media (min-width: $screen-md) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}
