.single_post_wrapper .single_post_banner {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (min-width: $screen-md) {
    padding-bottom: 33px;
  }

  &__img {
    max-width: 100%;
    width: 100%;
    @media (min-width: $screen-md) {
      max-width: 42%;
    }

    img {
      max-width:100%;
      width: 100%;
      display: block;
      border-radius: 14px;
      object-fit: cover;
      aspect-ratio: 7/4;
    }
  }

  &__border {
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, $purple 0%, rgba(119, 65, 229, 0) 100%);
    margin: 10px 0;
  }

  .author {
    display: flex;
    &__img {
      display: flex;
      width: 30px;
      height: 30px;
      justify-content: center;
      align-items: center;

      @media (min-width: $screen-md) {
        width: 50px;
        height: 50px;
      }

      img {
        border-radius: 100%;
        flex-shrink: 0;
        width: 22.5px;
        margin-right: 6px;
        height: 22.5px;
        object-fit: cover;

        @media (min-width: $screen-md) {
          width: 37.5px;
          height: 37.5px;
        }
      }
    }
    &__name {
      color: #5A5A5ACC;

    }
  }
  @media (min-width: $screen-md) {
    padding-bottom: 58px;
  }
  .blog_header {
    .title_area {
      margin-bottom: 10px;
      @media (min-width: $screen-md) {
        display: flex;
        margin-bottom: 40px;
      }
      .title {
        color: rgba(0, 0, 0, 0.80) !important;
        background: none !important;
        -webkit-text-fill-color: inherit;
        font-size: 18px;
        margin-bottom: 11px;
        @media (min-width: $screen-md) {
          display: block;
          font-size: 29px;
          margin-bottom: 0;
        }
      }
    }

    .blog_search {
      .form_wrapper {
        border: 1px solid rgba(52, 52, 52, 0.24);
      }
      .icon {
        svg {
          path {
            fill: rgba(155, 155, 155, 0.64);
          }
        }
      }
      input {
        background: none;
        color: #9B9B9BA3;

        &::-webkit-input-placeholder {
          /* Chrome/Opera/Safari */
          color: #9B9B9BA3;
        }
        &::-moz-placeholder {
          /* Firefox 19+ */
          color: #9B9B9BA3;
        }
        &:-ms-input-placeholder {
          /* IE 10+ */
          color: #9B9B9BA3;
        }
        &:-moz-placeholder {
          /* Firefox 18- */
          color: #9B9B9BA3;
        }
      }
    }

    .category_area {
      margin-bottom: 46px;
      @media (min-width: $screen-md) {
        display: block;
        margin-bottom: 85px;
      }
      .category_tabs {
        overflow-x: auto;
        display: flex;
        flex-wrap: nowrap;

        @media (min-width: $screen-sm) {
          flex-wrap: wrap;
        }

        li {
          flex-shrink: 0;
          &.active {
            a {
              background: $purple;

              .title {
                color: #fff;
              }

              .count {
                color: $purple;
                background: #EEEFF2;
              }
            }
          }
        }
        a {
          padding: 13px 12px;
          background: #5024A80F;
        }

        .title {
          color: #000000;
        }
        .count {
          display: flex;
          background: $purple;
          color: #FFFFFFCC;
        }
      }
    }

    @media (min-width: $screen-md) {
      padding-left: 50px;
      padding-right: 50px;
    }
  }

  .post_title_area {
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    &__breadcrumbs {
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      a {
        text-decoration: none;
      }

      &__item {
        color: #151F32;
        display: flex;
        align-items: center;

        @include fig(400, 13, 124%);
        letter-spacing: -0.26px;

        @media (min-width: $screen-md) {
          @include fig(400, 18, 124%);
          letter-spacing: -0.36px;
        }

        &__delimeter {
            margin: 0 20px;
            color: #151F32;
        }
      }
    }
    @media (min-width: $screen-md) {
      flex-direction: row;
    }

    &__content {
      @media (min-width: $screen-md) {
        max-width: 50%;
      }
    }

    h1 {
      @include pop(400, 30, 136%);
      letter-spacing: -0.6px;
      color: #fff;
      margin-bottom: 20px;

      @include titleGrad();

      @media (min-width: $screen-md) {
        max-width: 1120px;
        font-size: 53px;
        letter-spacing: -1.06px;
        line-height: 124%;
        margin-bottom: 40px;
      }
    }

    .post_meta {
      display: flex;
      flex-direction: column;
      margin: 20px 0;

      @media (min-width: $screen-md) {
        margin: 0;
      }
      color: #5A5A5ACC;


      > div {
        display: flex;
        align-items: center;
        @include fig(400, 15, 26px);
        @media (min-width: $screen-md) {
          font-size: 20px;
          line-height: 26px;
          margin-right: 30px;
        }
      }

      .date {
        display: flex;

        &__item {
          @include fig(400, 13, 200%);

          @media (min-width: $screen-md) {
            @include fig(400, 20, 26px);
            > {
              div {
                min-width: max-content;
              }
            }
          }
          margin-right: 10px;
          display: flex;
          align-items: center;
        }
      }

      .icon {
        width: 100%;
        max-width: 16px;
        height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        @media (min-width: $screen-md) {
          max-width: 24px;
          height: 24px;
        }
      }
    }
  }
}

.single_post {
  &__hero {
    @media (min-width: $screen-md) {
      padding: 0 50px;
    }
  }
}
