.block-page {
	background: #EEEFF2;
	padding-top: 88px;
	a {
		text-decoration: none;
		&:not(.btn, .button, .list-item__link, .acrobits_next_post__link, .list-item__link a, .acroPopup__button, .trustpilot, .toggle-list__list__item){
			border-bottom: 2px $purple solid;
			color: inherit;
			transition: .3s;
			&:hover {
				color: $purple;
			}
		}
	}
	.list-item__link, .other-industries__item, .industry-breadcrumbs {
		a {
			border: none !important;
		}
	}
}
