.incentive_panel {
  padding: 92px 0 20px 0;
  background: #eeeff2;
  @media (min-width: $screen-md) {
    padding: 130px 0 23px;
  }

  .title_area {
    margin-bottom: 22px;
    h3 {
      @include pop(400, 29, 35px);
      letter-spacing: -0.02em;
      text-align: center;
      @include titleGrad();
      margin-bottom: 16px;
      @media (min-width: $screen-md) {
        font-size: 35px;
        line-height: 124%;
        letter-spacing: -0.7px;
      }
    }

    p {
      @include pop(400, 16, 21px);
      letter-spacing: -0.02em;
      text-align: center;
      @include titleGrad();
      @media (min-width: $screen-md) {
        font-size: 23px;
        line-height: 136%;
        letter-spacing: -0.46px;
        margin-bottom: 32px;
      }
    }
  }

  .incentive_wrapper {
    flex-wrap: wrap;
    @media (min-width: $screen-md) {
      max-width: 1350px;
      margin: 0 auto;
      justify-content: center;
    }
    @media (min-width: $screen-lg) {
      flex-wrap: nowrap;
    }
  }
  .incentive_block {
    width: 100%;
    border-radius: 12px;
    background: #fff;
    overflow: hidden;
    padding: 24px;
    position: relative;

    img {
      width:100%;
      height: auto;
    }

    .info {
      position: absolute;
      text-align: center;
      width: 100%;
      left: 0;
      bottom: 95px;
      @include fig(400, 11, 15px);
      color: #7A50CE;
      display: flex;
      flex-direction: row;

      img {
        width: auto;
        height: 14px;
        margin-right: 5px;
      }

    }

    .info_white {
      position: absolute;
      text-align: center;
      width: 100%;
      left: 0;
      bottom: 95px;
      @include fig(400, 11, 15px);
      color: #7A50CE;
      display: flex;
      flex-direction: row;

      img {
        width: auto;
        height: 14px;
        margin-right: 5px;
      }

    }

    @media (min-width: $screen-sm) {
      max-width: calc(25% - 15px);
    }
    @media (min-width: $screen-md) {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      max-width: calc(16% - 15px);
    }
    @media (min-width: $screen-lg) {
      flex-wrap: nowrap;
    }

    .info_white {
      display: none;
    }

    &.active {
      border-radius: 12px;
      background: #42247f;
      box-shadow: 0px 1px 16px 0px rgba(66, 36, 127, 0.63);

      .title {
        color: #fff;
      }

      .info {
        display: none;
      }

      .info_white {
        display: flex;
      }

      button {
        &.add {
          opacity: 0;
          visibility: hidden;
          display: none;
        }

        &.remove {
          opacity: 1;
          visibility: visible;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      .info {
        color:#fff;
      }

      .info_white {
        color: #fff;
      }

    }
  }

  .block_wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    @media (min-width: $screen-md) {
      flex-direction: column;
    }

    .icon {
      width: 100%;
      max-width: 78px;
      @media (min-width: $screen-md) {
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 16px;
      }

      img {
        @media (min-width: $screen-md) {
          max-height: 95px;
          width: auto;
        }
      }
    }

    .title {
      width: 100%;
      margin-left: 16px;
      @include pop(400, 17, 136%);
      letter-spacing: -0.34px;
      color: #000;
      @media (min-width: $screen-md) {
        text-align: center;
        margin-left: 0;
        margin-bottom: 16px;
      }
    }
  }

  .block4 {

    .icon_hover {
      display: none;
    }


  }


  .active {
    .icon_show {
      display: none;
    }
    .icon_hover {
      display: block;
    }
  }

  .block2, .block3, .block4 {

    .info {
      justify-content: center;
      left: 18px;

      @media (min-width: $screen-md) {
        justify-content: center;
        left: 0;
      }

    }

    .info_white {
      justify-content: center;
      left: 18px;

      @media (min-width: $screen-md) {
        justify-content: center;
        left: 0;
      }

    }

  }


  .block6 {

    .info {
      position: unset;
      justify-content: flex-start;

      @media (min-width: $screen-md) {
        position: absolute;
        justify-content: center;
      }

    }

    .total_block {
      justify-content: flex-start;

      @media (min-width: $screen-md) {
        justify-content: center;
      }

    }

  }

  .block5 {

    .info {
      position: unset;

      @media (min-width: $screen-md) {
        position: absolute;
        justify-content: center;
      }

    }

  }

  .block_footer {
    display: flex;
    align-items: center;
    justify-content: center;
    @media (min-width: $screen-md) {
      margin-top: auto;
    }

    button {
      border-radius: 6px;
      border: 1.2px solid #9270d7;
      width: 100%;
      transition: ease all 0.2s;
      padding: 18px;
      color: #6436bf;
      @include pop(500, 12, 100%);

      &:hover {
        cursor: pointer;
      }

      &.base {
        border: 1.2px solid rgba(255, 255, 255, 0.4);
        opacity: 0.30000001192092896;
        color: #fff;
      }

      &.add {
        &:hover {
          background: #42247f;
          color: #fff;
        }
      }
      &.remove {
        letter-spacing: 0.12px;
        opacity: 0;
        visibility: hidden;
        display: none;
        color: #fff;
        &:hover {
          color: #42247f;
          background: #fff;
        }
      }
    }
  }

  .blox_plus {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    color: #768093;
    text-align: center;
    @include pop(400, 15, 136%);
    letter-spacing: -0.3px;
    @media (min-width: $screen-md) {
      max-width: 20px;
      width: 100%;
    }

    &.box_equals {
      @media (max-width: 1023px) {
        max-width: 100%;
      }
      @media (min-width: $screen-lg) {
        max-width: 20px;
      }
    }
    &.blox_or {
      @media (min-width: $screen-lg) {
        max-width: 30px;
      }
    }
  }

  .text_area {
    margin-left: 28px;
    @media (min-width: $screen-md) {
      margin-left: 0;
    }
  }

  .block5 {
    border-radius: 12px;
    border: 1px solid #db9daa;
    background: var(--background-50, #fff);
    box-shadow: 0px 0px 5px 0px rgba(211, 34, 70, 0.49);

    .block_wrapper {
      margin-bottom: 0;
    }

    .icon {
      width: 100%;
      max-width: 64px;
    }

    .title {
      width: 100%;
      @include pop(500, 16, 126%);
      letter-spacing: -0.34px;
      color: #24272e;
      margin-left: 0;
      margin-bottom: 6px;
    }

    .build_it_yourself_total {
      height: 36px;
      overflow: hidden;
      margin-bottom: 6px;
      @media (min-width: $screen-md) {
        margin: 25px 0;
      }
    }
    .build_it_yourself_total[data-scroll="0"] {
      .total_block {
        transform: translateY(0);
      }
    }
    .build_it_yourself_total[data-scroll="1"] {
      .total_block {
        transform: translateY(-36px);
      }
    }
    .build_it_yourself_total[data-scroll="2"] {
      .total_block {
        transform: translateY(-72px);
      }
    }
    .build_it_yourself_total[data-scroll="3"] {
      .total_block {
        transform: translateY(-108px);
      }
    }
    .total_block {
      @include pop(500, 29, 124%);
      letter-spacing: -0.58px;
      color: #dc526e;
      transition: ease all 0.3s;
    }

    .info {
      display: flex;
      align-items: center;

      img {
        width: auto;
        height: 14px;
      }
    }
  }

  .block6 {
    border-radius: 12px;
    background: var(--constants-white-100, #fff);
    box-shadow: 0px 0px 5px 0px #12be61;
    flex-direction: column;
    display: flex;

    .block_wrapper {
      margin-bottom: 0;
    }

    .icon {
      width: 100%;
      max-width: 60px;
    }

    .title {
      width: 100%;
      @include pop(500, 15, 126%);
      letter-spacing: -0.34px;
      color: #24272e;
      margin-left: 0;
      margin-bottom: 4px;
      white-space: nowrap;
      margin-top: 8px;
    }

    .cloud_softphone_total {
      height: 49px;
      overflow: hidden;
      margin-bottom: 6px;
      @media (min-width: $screen-md) {
        margin: 15px 0;
      }

      .total_block {
        display: flex;
        flex-direction: row;
        align-items: center;

        img {
            max-width: 30px;
        }

      }

    }

    .cloud_softphone_total[data-scroll="0"] {
      .total_block {
        transform: translateY(0);
      }
    }
    .cloud_softphone_total[data-scroll="1"] {
      .total_block {
        transform: translateY(-49px);
      }
    }
    .cloud_softphone_total[data-scroll="2"] {
      .total_block {
        transform: translateY(-98px);
      }
    }
    .cloud_softphone_total[data-scroll="3"] {
      .total_block {
        transform: translateY(-147px);
      }
    }
    .total_block {
      @include pop(500, 44, 112%);
      letter-spacing: -0.88px;
      color: #1ebd7b;
      transition: ease all 0.3s;
    }

    .info {
      display: flex;
      align-content: inherit;

      img {
        width: auto;
        height: 14px;
      }
    }

    .btn_group {
      margin-top: 24px;
      @media (min-width: $screen-md) {
        margin-top: auto;
      }
      a {
        box-shadow: 0px 1px 3px 0px rgba(39, 168, 125, 0.4),
          0px 4px 8px 0px rgba(39, 168, 125, 0.2),
          0px 6px 20px 0px rgba(39, 168, 125, 0.15);
        border-radius: 6px;
        background: #27b989;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        @include pop(500, 15, 100%);
        letter-spacing: 0.15px;
        @media (min-width: $screen-md) {
          height: 48px;
        }
      }
    }
  }

  .incentive_cta {
    box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
      0px 24px 60px rgba(6, 47, 125, 0.05),
      0px 12px 24px rgba(27, 59, 119, 0.05);
    border-radius: 12px;
    margin-top: 74px;
    display: flex;
    flex-direction: column;
    padding: 0 24px 32px;
    margin-bottom: 22px;
    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
      max-width: 1350px;
      margin: 74px auto 0;
    }

    @media (min-width: $screen-md) {
      flex-direction: row;
      align-items: center;
      // padding: 28px;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
        0px 24px 60px rgba(6, 47, 125, 0.05),
        0px 12px 24px rgba(27, 59, 119, 0.05);
      border-radius: 12px;
      padding: 0 32px 0 12px;
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: -65px;
      @media (min-width: $screen-md) {
        margin-top: 0;
        margin-right: 12px;
      }

      img {
        max-width: 208px;
        @media (min-width: $screen-md) {
          max-width: 106px;
        }
      }
    }

    .body {
      margin-bottom: 24px;
      @media (min-width: $screen-md) {
        margin-bottom: 0;
      }
    }

    .cta_title {
      @include pop(400, 23, 31px);
      color: #000;
      letter-spacing: -0.02em;
      text-align: center;
      margin-bottom: 16px;
      @media (min-width: $screen-md) {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 4px;
        text-align: left;
      }
    }

    .cta_desc {
      @include fig(400, 15, 20px);
      color: #4f4b58;
      text-align: center;
      @media (min-width: $screen-md) {
        text-align: left;
        font-size: 17px;
        line-height: 23px;
      }
    }

    .btn_group {
      align-items: center;
      justify-content: center;
      @media (min-width: $screen-md) {
        margin-left: auto;
      }

      a {
        padding: 18px 55px;
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
}
