.post_pagination {
  background: #eeeff2;
  .container {
    display: flex;
    flex-direction: column;
    padding: 0;
    @media (min-width: $screen-md) {
      flex-direction: row;
      padding: 0 20px;
      column-gap: 20px;
    }
  }

  .post_page {
    padding: 43px 20px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    text-decoration: none;
    position: relative;
    min-height: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    @media (min-width: $screen-md) {
      padding: 100px 80px;
      align-items: center;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(27, 29, 34, 0.5);
      z-index: 1;
    }
  }

  .small_title,
  .title {
    position: relative;
    z-index: 2;
  }

  .small_title {
    @include pop(500, 13, 124%);
    letter-spacing: 0.26px;
    color: #fff;
    margin-bottom: 10px;
    text-transform: uppercase;
    @media (min-width: $screen-md) {
      text-align: center;
    }
  }

  .title {
    @include pop(400, 23, 156%);
    letter-spacing: -0.46px;
    color: #fff;
    @media (min-width: $screen-md) {
      text-align: center;
    }
  }
}
