.other-industries {
	text-align: center;
	margin: 0 auto;
	max-width: 1440px;
	padding: 0 10px 40px 10px;
	@media (min-width: $screen-sm){
		padding: 0 30px 56px;
	}
	h2 {
		@include pop(400, 24, 1.24);
		margin-bottom: 24px;
		@media (min-width: $screen-sm){
			font-size: 29px;
		}
	}
	&__items {
		display: flex;
		overflow-x: auto;
		text-align: left;
		@media (min-width: $screen-sm){
			flex-wrap: wrap;
		}
	}
	&__item {
		min-width: calc(100vw - 60px);
		transition: .4s;
		padding: 2px;
		height: 261px;
		overflow: hidden;
		@media(min-width: $screen-sm){
			min-width: 25%;
			width: 25%;
		}
		a {
			height: 100%;
			display: flex;
			flex-direction: column;
			max-width: 100%;
			border-radius: 6px;
			transition: .4s;
			position: relative;
			align-items: center;
			justify-content: flex-end;
			background: #FFF;
			overflow: hidden;
			background-size: cover !important;
			img {
				max-width: 150%;
				@media(min-width: $screen-sm){
					max-width: 100%;
				}
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
			}
		}
		h3 {
			@include pop(400, 29, 1.24);
			letter-spacing: -0.02em;
			top: 24px;
			left: 24px;
			right: 24px;
			width: auto;
			position: absolute;
			@include titleGrad();
			display: flex;
			justify-content: space-between;
			align-items: center;
			z-index: 2;
			transition: .4s;
			svg {
				width: 36px;
				height: 36px;
				padding: 11px;
				border-radius: 50%;
				transition: .4s;
			}
		}
		@media (min-width: $screen-sm){
			&.active-item {
				width: 23%;
				min-width: 23%;
				height: 325px;
				opacity: 0.7;
				a {
					h3 {
						font-size: 24px;
					}
				}
				&.active-el {
					width: 31%;
					min-width: 31%;
					opacity: 1;
					a {
						h3 {
							font-size: 35px;
							svg {
								background: linear-gradient(180deg, rgba(58, 165, 211, 0.06) 0%, rgba(58, 165, 211, 0.09) 100%);
							}
						}
					}
				}
			}
			&.not-active-item {
				height: 199px;
				opacity: 0.7;
				a {
					h3 {
						font-size: 24px;
					}
					img {
						max-width: 80%;
					}
				}
			}
		}
	}
}
.toggle-list {
	margin: 0 10px 40px;
	border-radius: 16px;
	background: #fff;
	width: auto;
	@media (min-width: $screen-sm){
		display: none !important;
	}
	&__label {
		position: relative;
		padding: 26px 32px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		h3 {
			@include titleGrad();
			@include pop(400, 20, 1.36);
		}
		svg {
			width: 32px;
		}
	}
	&__list {
		display: none;
		padding: 0 24px 24px;
		width: auto;
		&__item {
			display: flex;
			padding: 10px 12px 10px 60px;
			position: relative;
			align-items: center;
			text-decoration: none;
			@include fig(500, 15, 1.06);
			color: #5A6272;
			border-radius: 10px;
			min-height: 52px;
			transition: .3s;
			margin-bottom: 2px;
			&:hover {
				background: #f1edfa;
				color: $purple;
			}
			&.current {
				color: #fff;
				background: $purple;
				img {
					filter: brightness(1000);
				}
			}
			img {
				width: 32px;
				position: absolute;
				left: 12px;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
}