.single_post {
	&_body {
		.hidden {
			display: none !important;
		}
		.scrollable-table-container {
			position: relative;
			&.scrolledLeft {
				.table-arrows .table-left-arrow {
					opacity: 0 !important;
					visibility: hidden;
				}
			}
			&.scrolledRight {
				.table-arrows .table-right-arrow {
					opacity: 0 !important;
					visibility: hidden;
				}
			}
			.table-arrows {
				position: absolute;
				top: 300px;
				left: 20px;
				width: 100%;
				padding: 0 20px;
				display: flex;
				justify-content: space-between;
				z-index: 10;
				.table-arrow {
					position: relative;
					width: 34px;
					height: 34px;
					border-radius: 50%;
					background: url('/wp-content/themes/acro/images/svg/arrow-right-white.svg') no-repeat center #602BCA;
					box-shadow: 0px 9px 33px 0px rgba(112, 72, 199, 0.20), 0px 1.127px 4.132px 0px rgba(112, 72, 199, 0.40);
					//top: 50%;
					//transform: translateY(-50%);
					z-index: 10;
					cursor: pointer;
					right: 20px;
					opacity: 1;
					transition: .3s;
					display: block;
					&:hover {
						opacity: 1;
					}
					&.table-left-arrow {
						//left: 20px;
						transform: rotate(180deg);
						background-position: 7px center;
					}
				}
			}
			&.active {
				.table-arrows {
					top: 300px;
					position: fixed;
				}
			}
			&.small-table {
				.table-arrows {
					top: 50%;
				}
			}
		}
		.scrollable-table-wrapper {
			position: relative;
			margin: 32px 0;
			box-shadow: 0 12px 24px 0 rgba(27,59,119,.05),0 24px 60px 0 rgba(6,47,125,.05),0 2px 4px 0 rgba(7,4,146,.1);
			border-radius: 12px;
			overflow: hidden;
			//max-height: 80vh;
			//overflow-y: auto;
			
			.scrollable-table {
				display: block;
				//white-space: nowrap;
				position: relative;
				
				table {
					margin: 0 !important;
					display: table;
					overflow-x: auto;
					border: none !important;
					background: none !important;
					width: 100% !important;
					tr {
						td, th {
							color: #4F4B58;
							@include fig(400, 20, 1.15);
							padding: 14px !important;
							border: none !important;
							position: relative;
							background: #fff !important;
							min-width: 170px;
							transition: .4s;
							opacity: .6;
							//&:first-child {
							//	@include pop(400, 22, 1.36);
							//	color: #000;
							//}
							@media (min-width: $screen-sm){
								padding: 24px !important;
								min-width: 206px;
							}
							&.active {
								opacity: 1;
							}
							&:before {
								box-shadow: inset 10px 0 8px -8px rgba(0, 0, 0, 0.1);
								position: absolute;
								top: 0;
								left: -30px;
								bottom: -1px;
								width: 30px;
								transform: translate(100%);
								transition: box-shadow .3s;
								content: "";
								pointer-events: none;
							}
							&:first-child {
								&:before {
									display: none;
								}
							}
							
							//&:last-child {
							//	&:before {
							//		display: none;
							//	}
							//}

							//&:first-child {
							//	@include pop(500, 22, 1.36);
							//	letter-spacing: -0.02em;
							//	//opacity: 0;
							//	//visibility: hidden;
							//	position: sticky;
							//	left: 0;
							//	z-index: 5;
							//	//box-shadow: 0px 2px 12px 0px rgba(0, 0, 0, 0.09);
							//	&:before {
							//		right: 0;
							//		left: auto;
							//	}
							//}
							&:not(:first-child) {
								z-index: 4;
							}
						}
						&:first-child {
							td, th {
								@include pop(500, 25, 1.1);
								letter-spacing: 0;
								color: #000 !important;
								&.active {
									background: var(--grad-linkup, linear-gradient(180deg, #DEF8EF 0%, #FFF 100%)) !important;
								}
							}
						}
						&:nth-child(even){
							th,td {
								background: linear-gradient(0deg, rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0.03) 100%), #FFF !important;
							}
						}
					}
				}
				&:nth-child(2){
					position: absolute;
					left: 0;
					top: 0;
					overflow: hidden;
					table {
						tr {
							td, th {
								opacity: 0;
								visibility: hidden;
								&:first-child {
									opacity: 1 !important;
									visibility: visible !important;
									z-index: 4;
								}
							}
						}
					}
				}
			}
		}
	}
}
