.blog_featured {
  .container {
    padding: 0;
    @media (min-width: $screen-md) {
      padding: 0 24px;
    }
  }

  .featured_post {
    position: relative;
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.64) 0%,
      rgba(0, 0, 0, 0.64) 100%
    );
    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background: rgba(27,29,34,.5);
      z-index: 2;
    }
  }

  .post_image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      @media (min-width: $screen-md) {
        // width: 100%;
      }
    }
  }

  .post_body {
    padding: 80px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 4;
    width: 100%;
    @media (min-width: $screen-md) {
      padding: 70px 0;
      max-width: 822px;
      margin: 0 auto;
    }

    .title {
      @include pop(400, 29, 124%);
      letter-spacing: -0.58px;
      color: #fff;
      text-align: center;
      margin-bottom: 20px;
      @media (min-width: $screen-md) {
        font-size: 35px;
        line-height: 116%;
        letter-spacing: -0.7px;
        margin-bottom: 10px;
      }
    }

    .desc {
      @include fig(400, 17, 156%);
      color: rgba(255, 255, 255, 0.8);
      text-align: center;
      margin-bottom: 20px;
      @media (min-width: $screen-md) {
        font-size: 20px;
        margin-bottom: 32px;
      }
    }

    .btn_group {
    }
    .btn {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      border: 1.2px solid rgba(255, 255, 255, 0.64);
      box-shadow: 0px 1px 3px 0px rgba(27, 29, 34, 0.12),
        0px 4px 8px 0px rgba(27, 29, 34, 0.08),
        0px 6px 20px 0px rgba(27, 29, 34, 0.06);
      .icon {
        width: 12px;
        height: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 16px;

        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
  }

  .incentive_cta {
    box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
      0px 24px 60px rgba(6, 47, 125, 0.05),
      0px 12px 24px rgba(27, 59, 119, 0.05);
    border-radius: 0px;
    margin-top: 74px;
    display: flex;
    flex-direction: column;
    padding: 0 24px 32px;
    margin-bottom: 40px;
    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
      margin: 15px 0 35px;
    }

    @media (min-width: $screen-md) {
      flex-direction: row;
      align-items: center;
      // padding: 28px;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
        0px 24px 60px rgba(6, 47, 125, 0.05),
        0px 12px 24px rgba(27, 59, 119, 0.05);

      padding: 0 32px 0 12px;
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: -65px;
      @media (min-width: $screen-md) {
        margin-top: 0;
        margin-right: 12px;
      }

      img {
        max-width: 208px;
        @media (min-width: $screen-md) {
          max-width: 106px;
        }
      }
    }

    .body {
      margin-bottom: 24px;
      @media (min-width: $screen-md) {
        margin-bottom: 0;
      }
    }

    .cta_title {
      @include pop(400, 23, 136%);
      color: #000;
      letter-spacing: -0.46px;
      text-align: center;
      margin-bottom: 8px;
      @media (min-width: $screen-md) {
        margin-bottom: 4px;
        text-align: left;
      }
    }

    .cta_desc {
      @include fig(400, 15, 20px);
      color: #4f4b58;
      text-align: center;
      @media (min-width: $screen-md) {
        text-align: left;
        font-size: 17px;
        line-height: 23px;
      }
    }

    .btn_group {
      align-items: center;
      justify-content: center;
      @media (min-width: $screen-md) {
        margin-left: auto;
      }

      a {
        padding: 18px 55px;
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
}
