.single_post_body {
  padding: 0 0 60px 0;
  display: flex;

  @media (min-width: $screen-md) {
    padding: 60px 10px 60px 10px;
  }

  @media (min-width: $screen-lg) {
    padding: 100px 75px;
  }

  .container {
    display: flex;
    justify-content: space-between;
    padding: 0;
    position: relative;

    @media (min-width: $screen-mlg) {
      max-width: 1440px;
    }
  }

  &__content {
    padding: 0 20px;
    max-width: 100%;
    @media (min-width: $screen-md) and (max-width: $screen-mlg) {
      max-width: 70%;
    }

    @media (min-width: $screen-mlg) {
      max-width: 750px;
    }

    a:not(.chapter-link) {
      text-decoration: none;
      border-bottom: 2px $purple solid;
      &:hover {
        color: $purple;
      }
    }
    .wp-caption, .wp-block-image, figure {
      max-width: 100%;
      p {
        font-size: 16px;
      }
    }
    .wp-caption-text {
      font-size: 16px;
    }

    .slick-arrow {
      display: flex;
      align-items: center;
      z-index: 1;

      svg {
        cursor: pointer;
        width: 40px;
        height: 40px;
      }
    }

    .chapters {
      display: none;


      @media (min-width: $screen-md) {
        display: block;
      }

      &__items {
        position: relative;
        margin-bottom: 20px;
        padding: 0 20px;

        &-list {
          display: flex;
          justify-content: space-between;
          padding:  {
            bottom: 40px;
            right: 20px;
            left: 20px;
          };
        }
      }

      &__arrow {
        display: none;
        cursor: pointer;
      }

      &__arrows {
        .prev, .next {
          position: absolute;
          top: 0;
          bottom: 0;
          width: 42px;
          height: 100%;
          padding-bottom: 40px;
          cursor: pointer;
          z-index: 30;
          svg {
            width: 100%;
            height: 100%;
          }
        }
        .prev {
          left: -10px;
          background: linear-gradient(90deg, rgba(255,255,255,1) 80%, rgba(255,255,255,0) 100%);
        }
        .next {
          right: -10px;
          left: auto;
          background: linear-gradient(90deg, rgba(255,255,255,0)  0%, rgba(255,255,255,1) 20%);
        }
      }

      &__item {
        display: flex;
        width: 227px;
        margin-right: 24px;
        height: 133px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 8px;
        flex: 1 0 0;
        border-radius: 12px;
        background: #FFF;
        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
        @include fig(500, 18, 136%);
        color: #000;

        &.current {
          background: $purple;
          box-shadow: 0px 1px 16px 0px rgba(66, 36, 127, 0.63);

          a {
            color: #fff;
          }
        }

        a {
          padding: 32px 24px;
          display: block;
          text-decoration: none;
        }
      }

      &__wrapper {
        text-align: center;

        display: none;

        @media (min-width: $screen-mlg) {
          display: block;
        }

        h5 {
          @media (min-width: $screen-mlg) {
            display: block;
          }

          display: none;
          background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          @include pop(400, 24, 124%);
        }
      }
    }

    .responsive-video {
      position: relative;
      padding-bottom: 56.25%;
      height: 0;
      overflow: hidden;
      max-width: 100%;
      background: #000;
      margin-bottom: 30px;
      iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  .pin-spacer {
    flex-basis: auto !important;
    order: -1;
  }

  &__table-of-contents {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 9999;

    @media (min-width: $screen-md) {
      position: relative;
      //max-width: 26%;
      z-index: 1;
      flex-basis: 200px;
      width: 200px;
      padding-right: 50px;
      flex-grow: 1;
      left: auto;
      right: auto;
      bottom: auto;
      top: auto;
      .table-of-contents__wrapper {
        &.fixed {
          position: fixed;
          top: 150px;
          max-width: 23%;
        }

        &.absolute {
          position: absolute;
          bottom: 0;
        }
      }
    }

    .table-of-contents {
      &__wrapper {
        h5 {
          color: var(--Text-500, #777184);
          @include pop(400, 18, 136%);
          padding: 16px 0;
          margin: 0;

          display: flex;
          align-items: center;
          justify-content: space-between;

          &.opened {
            .table-of-contents__arrow {
              svg {
                transform: rotate(0deg);
              }
            }
          }

          .table-of-contents__arrow {
            width: 32px;
            height: 32px;

            svg {
              transform: rotate(180deg);
            }

            @media (min-width: $screen-md) {
              display: none;
            }
          }
        }

        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
        border-radius: 16px 16px 0px 0px;
        background: #FFF;
        padding: 20px 24px;

        @media (min-width: $screen-mlg) {
          padding: 16px;
          border-radius: 17px;
          box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
        }

        display: flex;
        flex-direction: column;
        align-self: stretch;
      }

      &__items {
        display: none;

        @media (min-width: $screen-md) {
          display: block;
        }
      }

      &__item {
        display: flex;
        padding: 10px 12px;
        align-items: center;
        gap: 5px;
        @include fig(500, 15, 106%);

        &.active {
          background: $purple;

          a {
            color: #fff;
          }
        }

        a {
          text-decoration: none;
          color: #5A6272;
          text-overflow: ellipsis;
          text-wrap: nowrap;
          overflow: hidden;
        }
      }
    }
  }

  img {
    width: 100%;
    height: auto;
    display: block;
  }


  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul,
  ol {
    //padding: 0 20px;
    width: 100%;
  }

  table {
    margin: 32px 0;
    width: 100%;
    display: block;
    overflow-x: auto;
    //@media (min-width: $screen-md) {
    //  display: table;
    //}
    th {
      text-align: left;
    }
    
    th {
      color: $purple;
      @include fig(600, 20, 156%);
    }

    th, td {
      @include fig(400, 19, 156%);
      padding: 15px;
      vertical-align: top;
    }

    table, th, td {
      border: 1px solid #D9D9D9;
    }
  }

  h2 {
    @include pop(400, 29, 124%);
    letter-spacing: -0.58px;
    @include titleGrad();
    margin-bottom: 20px;
    @media (min-width: $screen-md) {
      font-size: 35px;
      letter-spacing: -0.7px;
      margin-bottom: 16px;
    }
  }

  h3 {
    @include pop(400, 29, 124%);
    letter-spacing: -0.58px;
    @include titleGrad();
    margin-bottom: 20px;
  }

  h1 {
    @include pop(400, 64, 124%);
    letter-spacing: -1.28px;
    @include titleGrad();
    margin-bottom: 20px;
  }

  h4 {
    @include pop(400, 24, 124%);
    letter-spacing: -0.48px;
    @include titleGrad();
    margin-bottom: 20px;
  }

  h5 {
    @include pop(400, 20, 124%);
    letter-spacing: -0.4px;
    @include titleGrad();
    margin-bottom: 20px;
  }

  h6 {
    @include pop(400, 16, 124%);
    letter-spacing: -0.32px;
    @include titleGrad();
    margin-bottom: 20px;
  }


  .form {
    text-align: center;

    p {
      margin-bottom: 0 !important;
      color: $green;
      font-size: 18px;
    }
  }

  p {
    @include fig(400, 20, 190%);
    color: #1e1c21;
    margin-bottom: 20px;

    //img.size-full {
    //  margin-left: -20px;
    //  margin-right: -20px;
    //  width: calc(100% + 40px);
    //  max-width: none;
    //  @media (min-width: $screen-md) {
    //    margin-left: -100px;
    //    margin-right: -100px;
    //    width: calc(100% + 200px);
    //  }
    //}
  }

  ul,
  ol {
    padding: 0 30px 20px 30px;

    li {
      @include fig(400, 17, 156%);
      color: #1e1c21;
      margin-bottom: 5px;
      &::marker {
        margin-right: 30px;
        display: inline-block;
      }
      ul, ol {
        padding: {
          left: 30px;
          bottom: 0;
        };
        li {
          margin-bottom: 0;
        }
      }
    }
  }

  ol li {
    list-style: decimal;
  }

  ul li {
    list-style: disc;
  }

  blockquote {
    background: #fff;
    padding: 32px 30px;
    margin: 0 0 40px;
    border-left: 7px solid $purple;
    @media (min-width: $screen-md) {
      padding: 32px 30px;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
    }
    position: relative;

    p {
      padding: 0;
      margin: 0;
    }
    h1,h2,h3,h4,h5,h6 {
      padding-left: 0;
    }

    &:before {
      content: '';
      display: block;
      height: 16px;
      width: 22px;
      margin-bottom: 10px;
      background-image: url('data:image/svg+xml,<svg width="22" height="24" viewBox="0 0 22 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M3 22.2486L9 1.2486" stroke="%239881C5" stroke-width="6"/><path d="M13 21.8823L19 0.882263" stroke="%239881C5" stroke-width="6"/></svg>');
    }
  }
}
