.post_block {
  display: flex;
  flex-direction: column;
  background: #fff;
  margin-bottom: 10px;
  box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05),
    0px 24px 60px 0px rgba(6, 47, 125, 0.05),
    0px 2px 4px 0px rgba(7, 4, 146, 0.1);
  text-decoration: none;
  position: relative;
  width: 100%;
  @media (min-width: $screen-sm) {
    max-width: calc(50% - 10px);
    margin: 0 5px 10px;
  }
  @media (min-width: $screen-md) {
    max-width: calc(25% - 20px);
    margin: 0 10px 20px;
  }

  .post_image {
    padding: 8px;

    img {
      object-fit: cover;
      width: 100%;
      height: auto;

      @media (min-width: $screen-md) {
        aspect-ratio: 16 / 9;
        object-fit: cover;
      }
    }
  }


  .post_body {
    display: flex;
    flex-direction: column;
    padding: 8px 20px 20px;
    @media (min-width: $screen-sm) {
      flex-grow: 1;
    }

    .tags {
      display: flex;
      position: absolute;
      top: 14px;
      flex-direction: column;
      left: 14px;
      align-items: flex-start;
    }

    .tag {
      @include fig(500, 13, 100%);
      color: #fff;
      text-decoration: none;
      padding: 10px 6px;
      background: #6436bf;
      margin-bottom: 2px;
    }

    .title {
      @include pop(400, 20, 136%);
      color: #000;
      margin-bottom: 10px;
    }

    .desc {
      @include fig(400, 15, 126%);
      color: #777184;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 3; /* number of lines to show */
      line-clamp: 3;
      -webkit-box-orient: vertical;
      @media (min-width: $screen-sm) {
        line-height: 136%;
      }
    }

    .btn_group {
      display: none;

      @media (min-width: $screen-sm) {
        display: flex;
        padding-top: 20px;
        margin-top: auto;
      }

      .btn {
        color: #6436bf;
        @include pop(500, 13, 124%);
        letter-spacing: 0.26px;
        text-transform: uppercase;
        margin: 0;
        padding: 0;
      }
    }
  }

  &.small_card {
    flex-direction: row;

    .post_image {
      width: 100%;
      max-width: 86px;
      height: 86px;
      padding: 10px;

      img {
        height: 100%;
        object-fit: cover;
      }
    }

    .post_body {
      padding: 8px 15px 8px 5px;
      justify-content: center;
    }

    .title {
      font-size: 15px;
      line-height: 156%;
      margin-bottom: 0;
    }

    .desc {
      display: none;
    }

    .tags {
      position: relative;
      flex-direction: row;
      flex-wrap: wrap;
      top: 0;
      left: 0;

      .tag {
        padding: 0 0 10px 0;
        background: none;
        color: #6436bf;
        margin-bottom: 0;
        margin-right: 10px;
        font-size: 13px;
      }
    }
  }
}
