.single_post_body__content {
  .single-post-faq {
    width: 95%;


    margin-top: 60px;

    @media (min-width: $screen-md) {
      padding: {
        top: 70px;
        left: 60px;
        right: 60px;
        bottom: 50px;
      }
      box-shadow: 0px 12px 24px 0px #1B3B770D;
    }

    &__title {
      font-size: 36px;
    }

    &__subtitle {
      font-size: 24px;
    }

    h2 {
      margin-bottom: 50px;
     }
    ol {
      & > li {
        list-style: decimal;
      }
    }

    ul.single-post-faq__items {
      list-style-type: none;
      padding: 0 10px 40px 10px;
      @media (min-width: $screen-md) {
        padding: 0;
      }
    }

    li {
      &.single-post-faq__item__question {
        h3 {
          color: #6436BF;
          background: none;
          margin-bottom: 10px;
          @include pop(400, 22, 29.92px);
          -webkit-text-fill-color: initial; /* Resets to the default text color */
          padding: {
            left: 0;
            right: 0;
          }
        }
        &::marker {
          color: rgba(0, 0, 0, 0);
        }
      }

      &.single-post-faq__item__answer {

        color: #4F4B58;
        @include fig(400, 18, 28px);
        &::marker {
          color: rgba(0, 0, 0, 0);
        }
      }
    }

    .single-post-faq__line {
      margin: {
        top: 20px;
        bottom: 20px;
      }
      border-bottom: 1px solid #D1D5DB;
    }
  }
}
