.newsletter_bar {
  padding: 64px 0;
  background: #321B5F;

  .newsletter_bar_inner {
    display: flex;
    flex-direction: column;
    gap: 32px;
    justify-content: center;
    align-items: center;

    @media (min-width: $screen-sm) {
      flex-direction: row;
    }

    .content {
      @include pop(400, 23, 136%, normal);
      color: #fff;
      letter-spacing: -0.46px;
      text-align: center;
    }

    .form {
      order: 4;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05),
      0px 24px 60px 0px rgba(6, 47, 125, 0.05),
      0px 2px 4px 0px rgba(7, 4, 146, 0.1);
      padding: 6px;
      @media (min-width: $screen-sm) {
        order: 3;
        margin-top: 0;
      }

      form {
        display: flex;
        flex-wrap: wrap;
      }

      .hs_email {
        width: 100%;
        @media (min-width: $screen-sm) {
          max-width: 60%;
        }
        label {
          display: none;
        }

        input {
          width: 100%;
          background: none;
          border: none;
          outline: none;
          margin: 0;
          padding: 19px 10px;
          box-shadow: none;
          @include fig(400, 18, 100%);
          color: #8c8698;
          text-align: center;
          @media (min-width: $screen-sm) {
            text-align: left;
            padding-left: 16px;
          }
        }
      }

      .hs_submit {
        width: 100%;
        @media (min-width: $screen-sm) {
          max-width: 40%;
        }

        .actions {
          height: 100%;
        }
        input {
          width: 100%;
          background: none;
          border: none;
          outline: none;
          margin: 0;
          padding: 19px 10px;
          box-shadow: none;
          border-radius: 6px;
          background: var(--secondary-400, #27b989);
          /* shadow green button */
          box-shadow: 0px 1px 3px 0px rgba(39, 168, 125, 0.4),
          0px 4px 8px 0px rgba(39, 168, 125, 0.2),
          0px 6px 20px 0px rgba(39, 168, 125, 0.15);
          @include pop(500, 16, 100%);
          letter-spacing: 0.16px;
          color: #fff;
          height: 100%;
          white-space: nowrap;
        }
      }

    }

  }
  &.newsletter_bottom_bar {
    transition: .5s;
    &.enable_sticky {
      opacity: 0;
      &.sticky-bar {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 10000;
        opacity: 1;
        padding: 30px 0;
        .acro-close-btn {
          position: absolute;
          right: 10px;
          top: 20px;
          width: 20px;
          height: 20px;
          display: block;
          cursor: pointer;
          @media (min-width: $screen-sm) {
            right: 20px
          }
          &:before, &:after {
            content: '';
            display: block;
            width: 20px;
            height: 2px;
            background: #fff;
            transform: rotate(45deg);
            position: absolute;
            top: 8px;
          }
          &:after {
            transform: rotate(-45deg);
          }
        }
      }
      .newsletter_bar_inner {
        .form {
          .hs_email input {
            padding: 14px 10px;
          }
          .hs_submit {
            input[type=submit] {
              padding: 15px 10px;
            }
          }
        }
      }
    }
  }

}