.feature-showcase-section {
    width: 100%;
    padding-top: 50px;
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    overflow: hidden;

    @media (max-width: 767px) {
        padding-top: 30px;
        padding-bottom: 30px;
    }


    &_cont {
        width: calc(100% - 200px);
        display: flex;
        flex-direction: column;
        gap: 45px;
        max-width: 100%;

        @media (max-width: 1024px) {
            width: calc(100% - 60px);
        }

        @media (max-width: 992px) {
            width: calc(100% - 40px);
        }

        @media (max-width: 767px) {
            width: calc(100% - 0px);
            gap: 30px;
            padding-left: 25px;
            padding-right: 25px;
        }

    }

    &_top {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
        gap: 30px;
        align-items: start;
        width: 100%;
        position: relative;

        @media (max-width: 992px) {
            grid-template-columns: 1fr;
            gap: 30px;
            display: flex;
            flex-direction: column;
        }

        @media (max-width: 767px) {
            gap: 20px;
        }
    }

    .feature-showcase-grid {
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 100%;
        min-width: 0;

        @media (max-width: 992px) {
            gap: 10px;
        }

        .feature-showcase-item {
            display: flex;
            gap: 24px;
            padding: 28px;
            border-radius: 12px;
            border: 1px solid #ECEDEE;
            background: #FFF;
            align-items: center;
            width: 100%;
            min-width: 0;

            @media (max-width: 992px) {
                flex-direction: column;
                gap: 14px;
                align-items: flex-start;
                padding: 25px;
            }

            @media (max-width: 767px) {
                padding: 20px;
            }

            .feature-showcase-icon {
                flex-shrink: 0;
            }

            .feature_showcase_icon_cont {
                width: 53px;
                height: 53px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 57.139px;
                background: var(--white, #FFF);
                box-shadow: 0px 9.9px 36.3px 0px rgba(162, 165, 168, 0.20), 0px 1.24px 4.545px 0px rgba(162, 165, 168, 0.41);

                img {
                    width: auto;
                    height: auto;
                    max-width: 100%;
                }
            }

            .feature-showcase-text {
                flex: 1;
                min-width: 0;
                color: rgba(0, 0, 0, 0.50);
                font-family: Figtree, Helvetica, sans-serif;
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 20px;
                word-wrap: break-word;
            }
        }
    }


    .feature-showcase-image {
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 100%;
        min-width: 0;

        @media (max-width: 767px) {
            gap: 15px;
        }

        .feature-showcase-swiper {
            width: 100%;
            border-radius: 15px;
            overflow: hidden;
            min-width: 0;
        }

        .feature-showcase-slide {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            min-width: 0;

            img {
                max-width: 100%;
                max-height: 100%;
                width: auto;
                height: auto;
                object-fit: contain;
            }
        }

        .feature-showcase-image_button {
            width: 100%;
            min-width: 0;
            
            .feature-showcase-cta-button {
                width: 100%;
                padding: 21px 0;
                height: 60px;
                white-space: normal;
            }
        }
    }

    .feature-showcase-section_bottom {
        width: 100%;
        padding: 30px 0;
        display: flex;
        justify-content: center;
        background-size: cover !important;
        border-radius: 20px;
        background: #FFF;
        box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);

        @media (max-width: 992px) {
            padding: 30px 25px;
            border-radius: 20px;
            background: #FFF;
            box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
            background-size: 183% 120%;
            order: 3;
        }

        @media (max-width: 767px) {
            padding: 30px 25px;
            border-radius: 20px;
            background: #FFF;
            box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
            background-size: 600% 120%;
        }



        .feature-showcase-cta-content {
            width: 80%;
            display: flex;
            gap: 40px;
            justify-content: center;
            align-items: center;

            @media (max-width: 1024px) {
                width: 90%;
            }

            @media (max-width: 992px) {
                flex-direction: column;
                gap: 14px;
                width: 100%;
            }

            @media (max-width: 767px) {
                flex-direction: column;
                gap: 14px;
                width: 100%;
            }




        }

        .feature-showcase-cta-text {
            text-align: center;
            font-family: Poppins, Helvetica, sans-serif;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 124%;
            /* 29.76px */
            letter-spacing: -0.48px;
            background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;


            @media (max-width: 992px) {
                text-align: center;
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 24.8px */
                letter-spacing: -0.4px;
            }


            @media (max-width: 767px) {
                text-align: center;
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 24.8px */
                letter-spacing: -0.4px;
            }


        }
    }

}

.show-on-mobile {
    display: none !important;

    @media (max-width: 992px) {
        display: flex !important;
    }

    @media (max-width: 767px) {
        display: flex !important;
    }


}

.show-on-desktop {
    @media (max-width: 992px) {
        display: none !important;
    }

    @media (max-width: 767px) {
        display: none !important;
    }


}


/* Slider container styles */
.feature-showcase-swiper,
.feature-showcase-swiper-mobile {
    width: 100%;
}

/* Individual slide styles */
.feature-showcase-slide {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    

}

/* Slide image styles */
.feature-showcase-slide img {
    width: 100% !important;
    height: auto !important;
    object-fit: cover;
    opacity: 0

}