.archive.category,
.search-results {
  .blog_wrapper .filter {
    display: none;
  }

  .blog_post_wrapper {
    .post_block {
      &:first-child {
        @media (min-width: $screen-sm) {
          max-width: calc(50% - 10px) !important;
        }
        @media (min-width: $screen-md) {
          max-width: calc(50% - 20px) !important;
        }

        .title {
          font-size: 30px;
          line-height: 136%;
        }

        .post_image {
          img {
            @media (min-width: $screen-sm) {
              max-height: 236px;
              object-fit: cover;
            }
          }
        }
      }
    }

    .cta_post_block {
      padding: 20px 20px 40px 20px;

      img {
        max-width: 200px;
        margin: 0 auto;
      }

      .title {
        @include pop(400, 23, 136%, normal);
        letter-spacing: -0.46px;
        text-align: center;
        margin-bottom: 8px;
      }

      .content {
        @include fig(400, 15, 136%, normal);
        color: #4F4B58;
        text-align: center;
        margin-bottom: 20px;
      }

      a {
        display: flex;
        width: 152px;
        height: 56px;
        padding: 0px 16px;
        justify-content: center;
        align-items: center;
        border-radius: 6px;
        border: 1.6px solid #BABFC9;
        @include pop(400, 15, 136%, normal);
        color: #4F4B58;
        margin: 0 auto;
        text-decoration: none;
        transition: 0.3s;

        &:hover {
          color: #fff;
          background: #4F4B58;
        }

      }

    }

  }
}
