.single_post_body__author-info {
  //max-width: 300px;
  //margin-left: 50px;
  flex-basis: 300px;
  flex-grow: 1;
  display: none;
  padding: {
    left: 85px;
  };

  &.active {
    .newsletter_bar.always_sticky {
    }
  }

  //.pin-spacer {
  //  flex-basis: auto !important;
  //  order: -1;
  //}

  @media (min-width: $screen-mlg) {
    display: block;
  }

  .author_info {
    &__img {
      position: relative;
      img {
        border-radius: 16px;
      }

      .author_info__roles {
        font-family: "DM Sans", sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: 20px;
        padding: 11px 8px 11px 18px;
        border-radius: 6px;
        background: $green;
        color: #fff;
        text-transform: capitalize;
        position: absolute;
        max-width: 190px;
        right: 0;
        bottom: 6px;
        width: 100%;
      }
    }

    &__content {
      display: flex;
      flex-shrink: 0;
      flex-direction: column;
      background-color: #fff;
      border-radius: 16px;
      box-shadow: 0px 5px 14px 0px rgba(8, 15, 52, 0.04);
      padding: 25px 37px 25px 30px;
      a {
        text-decoration: none;
      }
    }

    &__title {
      color: #101111;
      @include pop(400, 14, 124%);
      margin-bottom: 10px;
      letter-spacing: -0.28px;
    }

    &__name {
      color: #101111;
      @include pop(400, 28, 124%);
      letter-spacing: -0.56px;
      margin-bottom: 10px;
    }

    &__roles {
      @include pop(500, 16, 124%);
      letter-spacing: -0.32px;
      color: $purple;
      margin: 8px 0;
      text-transform: capitalize;
    }

    &__bio {
        @include fig(400, 15, 156%);
        color: #101111;
        letter-spacing: 0.15px;
        margin-bottom: 8px;
    }

    margin-bottom: 50px;
    
    &__socials {
      display: flex;
      gap: 16px;
    }
    &__social {
      width: 37px;
      height: 37px;
      a {
        &:hover {
          rect {
            fill: $purple;
          }
        }
      }
    }
  }

  ul {
    padding: 0 !important;
    color: red;
    list-style: none !important;
    li {
      list-style: none !important;

      label {
        padding: 0 11px;
        display: block !important;
        &.hs-main-font-element {
          @include fig(400, 13, 156%);
          color: red;
        }
      }
    }
  }

  .newsletter_bar {
    background: $purple;
    border-radius: 16px;
    gap: 16px;
    padding: 32px;
    position: relative;
    box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
    z-index: 2;
    overflow: hidden;
    animation: .3s translate ease;

    &.active {
      transform: translateY(50px);
    }

    &.sticky {
      top: 168px !important;
    }

    &.absolute {
      position: absolute;
    }

    .form__svg-bg {
      position: absolute;
      bottom: -16%;
      right: -11%;
      z-index: -1;
      opacity: 0.4;
    }

    &_inner {
      flex-direction: column;

      .title {
        @include pop(400, 28, 124%);
        text-align: center;
        color: #fff;
      }

      .content {
        @include fig(500, 20, 156%)
      }
    }

    .form {
      .hs_submit {
        max-width: 100%;
      }

      .hs_email {
        max-width: 100%;
      }
    }
  }
}
