.industry-chapters {
	.chapter {
		border-radius: 10px;
		transition: .3s;
		padding: 10px 12px;
		display: flex;
		@include fig(500, 15, 1.06);
		color: #5A6272;
		position: relative;
		margin-bottom: 2px;
		cursor: pointer;
		align-items: center;
		min-height: 52px;
		&.current {
			background: #F1EDFA;
			color: $purple;
		}
		@media(min-width: $screen-sm){
			&:hover {
				background: #F1EDFA;
				color: $purple;
			}
		}
		svg {
			width: 32px;
			position: absolute;
			left: 12px;
			top: 50%;
			transform: translateY(-50%);
		}
	}
}