.feature-animated-cta {
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    padding: 50px 0;

    @media (max-width: 767px) {
        padding: 30px 25px;
    }


    &_container {
        width: calc(100% - 200px);
        display: flex;
        padding: 80px 63px;
        border-radius: 24px;
        background: #FFF;
        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05);
        overflow: hidden;
        background-size: cover !important;

        @media (max-width: 1024px) {
            width: calc(100% - 0px);
            padding: 60px 25px;
        }

        @media (max-width: 992px) {
            width: calc(100% - 0px);
            padding: 60px 25px;
            flex-direction: column;
        }

        @media (max-width: 768px) {
            width: calc(100% - 0px);
            padding: 60px 0px 0;
            flex-direction: column;
        }

        &_txt {
            width: 55%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            @media (max-width: 1023px) {
                width: 100%;
                padding: 0px 25px 25px;
            }

            @media (max-width: 992px) {
                width: 100%;
                padding: 0px 25px 25px;
            }

            @media (max-width: 768px) {
                width: 100%;
                padding: 0px 25px 25px;
            }

            .feature-animated-cta_title {
                font-family: Poppins, sans-serif;
                font-size: 35px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 43.4px */
                letter-spacing: -0.7px;
                background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-bottom: 10px;
                width: 100%;


                @media (max-width: 1023px) {
                    text-align: center;
                }

                @media (max-width: 768px) {
                    text-align: center;
                    font-family: Poppins;
                    font-size: 28px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 124%;
                    /* 34.72px */
                    letter-spacing: -0.56px;
                }
            }

            .feature-animated-cta_text {

                @media (max-width: 1023px) {
                    width: 100%;
                }

                @media (max-width: 768px) {
                    width: 100%;
                }

                p {
                    font-family: Figtree, sans-serif;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 124%;
                    /* 22.32px */
                    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                    background-clip: text;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    display: flex;
                    align-items: center;
                    gap: 8px;


                    @media (max-width: 1023px) {
                        flex-direction: column;
                        width: 100%;
                    }

                    @media (max-width: 768px) {
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 124%;
                        /* 19.84px */
                        flex-direction: column;
                        width: 100%;
                    }
                }
            }

            .feature-animated-cta_button {
                margin-top: 40px;
                width: 100%;
                display: flex;
                justify-content: start;

                @media (max-width: 1023px) {
                    justify-content: center;
                }

                @media (max-width: 768px) {
                    justify-content: center;
                }
            }
        }


        .feature-animated-cta_images {
            width: 45%;
            position: relative;

            @media (max-width: 1023px) {
                width: 100%;
            }

            @media (max-width: 768px) {
                width: 100%;
            }

            .feature-animated-cta_images_cont {
                width: 63%;
                position: absolute;
                bottom: -90%;
                right: -8%;

                @media (max-width: 1023px) {
                    width: 100%;
                    position: relative;
                    bottom: -73px;
                    right: -102px;
                }

                @media (max-width: 768px) {
                    width: 100%;
                    position: relative;
                    bottom: -73px;
                    right: -102px;
                }

                img {
                    position: absolute;
                    bottom: 0;
                    right: 0%;

                }

                .feature-animated-cta_image_1 {
                    position: relative;
                    z-index: 1;
                    transition: all 0.5s ease-in-out;

                    @media (max-width: 465px) {
                        width: 80%;
                    }
                }

                .feature-animated-cta_image_2 {
                    z-index: 2;
                    bottom: 30px;
                    right: 47%;
                    transition: all 0.5s ease-in-out;

                    @media (max-width: 767px) {
                        right: 64%;
                    }

                    @media (max-width: 465px) {
                        width: 38%;
                        bottom: 49px;
                        right: 72%;
                    }
                }

                .feature-animated-cta_image_3 {
                    z-index: 3;
                    bottom: 30px;
                    right: 52%;
                    transition: all 0.5s ease-in-out;


                    @media (max-width: 767px) {
                        right: 86%;
                    }

                    @media (max-width: 465px) {
                        width: 38%;
                        bottom: 76px;
                        right: 90%;
                    }
                }
            }

            .feature-animated-cta_image_sm {
                width: 100%;
            }
        }
    }


    @media (min-width: 1023px) {
        &.hover-active {
            .feature-animated-cta_image_2 {
                bottom: 43px !important;
                right: 80% !important;
                transition: all 0.5s ease-in-out;
            }

            .feature-animated-cta_image_3 {
                bottom: 50px !important;
                right: 117% !important;
                transition: all 0.5s ease-in-out;
            }
        }
    }

    .feature-animated-cta_image_sm_cont {
        padding: 0px 0px 0px 25px;
        justify-content: center;
    }
}