.two-cols-image-text-button {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 120px 15px 100px 0;

    @media(max-width: 992px) {
        padding: 50px 0px 0px 0px;
        margin-top: 60px;
    }

    @media(max-width: 767px) {
        padding: 50px 0px 0px 0px;
        margin-top: 60px;
    }

    &_container {
        width: calc(100% - 200px);
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 51px;

        @media(max-width: 992px) {
            width: calc(100% - 0px);
            flex-direction: column;
            gap: 20px;
        }

        @media(max-width: 767px) {
            width: calc(100% - 0px);
            flex-direction: column;
            gap: 20px;
        }

        .text_col {

            @media(max-width: 992px) {
                padding: 0px 20px 44px;
                justify-content: center;
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            @media(max-width: 767px) {
                padding: 0px 20px 44px;
                justify-content: center;
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            .title {
                color: #515354;
                font-family: Poppins, Helvetica, sans-serif;
                font-size: 50px;
                font-style: normal;
                font-weight: 400;
                line-height: 109%;
                /* 54.5px */
                letter-spacing: -1px;
                margin-bottom: 20px;


                @media(max-width: 992px) {
                    color: #515354;
                    text-align: center;
                    font-size: 35px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 124%;
                    /* 43.4px */
                    letter-spacing: -0.7px;
                    margin-bottom: 24px;
                }


                @media(max-width: 767px) {
                    color: #515354;
                    text-align: center;
                    font-size: 35px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 124%;
                    /* 43.4px */
                    letter-spacing: -0.7px;
                    margin-bottom: 24px;
                }
            }

            .text {
                margin-bottom: 31px;

                p {
                    color: #4F4B58;
                    font-family: Figtree, Helvetica, sans-serif;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 128%;
                    /* 25.6px */
                    margin-bottom: 10px;

                    @media(max-width: 992px) {
                        color: #4F4B58;
                        text-align: center;
                        font-size: 17px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 127%;
                        /* 21.59px */
                    }

                    @media(max-width: 767px) {
                        color: #4F4B58;
                        text-align: center;
                        font-size: 17px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 127%;
                        /* 21.59px */
                    }
                }
            }

        }



        .img_col {
            img {
             

                @media(max-width: 767px) {
                    width: 128%;
                    margin-left: -16%;
                }
            }
        }
    }
}