.feature-cards-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;

    @media (max-width: 767px) {
        padding-top: 30px;
        padding-bottom: 30px;
        padding-left: 25px;
        padding-right: 25px;
    }



    .feature-cards-section_cont {
        width: calc(100% - 200px);
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;

        @media (max-width: 992px) {
            width: calc(100% - 0px);
        }

        @media (max-width: 767px) {
            width: calc(100% - 0px);
        }


        .feature-cards-section_title {
            width: 70%;
            text-align: center;
            font-family: Poppins, sans-serif;
            font-size: 44px;
            font-style: normal;
            font-weight: 400;
            line-height: 124%;
            /* 54.56px */
            letter-spacing: -0.88px;
            background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;


            @media (max-width: 767px) {
                text-align: center;
                font-size: 28px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 34.72px */
                letter-spacing: -0.84px;
                background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }

    .feature-cards-grid {
        width: 100%;
        display: flex;
        border-radius: 12px;
        border: 1px solid #ECEDEE;
        background: #FFF;
        box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);


        @media (max-width: 767px) {
            flex-direction: column;
        }

        .feature-card {
            width: 34%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
            padding: 45px 30px;
            position: relative;
            /*max-height: 410px;*/

            @media (max-width: 992px) {
                width: 100%;
                padding: 30px 25px;
            }

            @media (max-width: 767px) {
                width: 100%;
                padding: 30px 25px;
            }

            &:before {
                content: '';
                position: absolute;
                width: 1px;
                height: 82%;
                background: #ECEDEE;
                top: 8%;
                right: 0;

                @media (max-width: 767px) {
                    width: 80%;
                    height: 1px;
                    top: 100%;
                    right: 10%;
                }
            }

            .feature-card_header {
                width: 100%;
                display: flex;
                align-items: start;
                gap: 10px;
                flex-direction: column;

                &_title {
                    display: flex;
                    align-items: start;
                    gap: 12px;
                    justify-content: space-between;
                    width: 100%;
                }

                .feature-card_title {
                    width: 80%;
                    color: #1E1C21;
                    font-family: Poppins, sans-serif;
                    font-size: 23px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 136%;
                    /* 31.28px */
                    letter-spacing: -0.46px;

                    @media (max-width: 992px) {
                        color: #1E1C21;
                        font-size: 20px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 128%;
                        /* 25.6px */
                        letter-spacing: -0.4px;
                    }

                    @media (max-width: 767px) {
                        color: #1E1C21;
                        font-size: 20px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 128%;
                        /* 25.6px */
                        letter-spacing: -0.4px;
                    }
                }

                .feature-card_icon {
                    width: 20%;
                    display: flex;
                    justify-content: flex-end;

                    .feature_card_icon_cont {
                        display: flex;
                        width: 44px;
                        height: 44px;
                        justify-content: center;
                        align-items: center;
                        border-radius: 12px;
                        background: #FFF;
                        box-shadow: 0px 2px 10px 0px rgba(124, 141, 181, 0.12);
                    }
                }

                .feature-card_description {
                    width: 100%;
                    display: flex;
                    align-items: start;
                    gap: 12px;

                    &_icon {
                        display: flex;
                        width: 20px;
                        height: 20px;
                        justify-content: center;
                        align-items: center;
                    }

                    &_text {
                        color: #8C8698;
                        font-family: Figtree, sans-serif;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 124%;
                        /* 19.84px */
                    }
                }
            }

            .feature-card_image {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    max-width: 100%;
                }
            }

        }
    }
}