.features-availables {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;

    &_container {
        width: calc(100% - 200px);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 32px;


        @media(max-width: 1024px) {
            width: calc(100% - 0px);
        }


        @media(max-width: 992px) {
            width: calc(100% - 0px);
        }

        @media(max-width: 768px) {
            width: calc(100% - 0px);
            overflow-x: hidden;
        }



        .features-availables_top {
            width: 100%;

            .features-availables_title {
                text-align: center;
                font-family: Poppins, sans-serif;
                font-size: 29px;
                font-style: normal;
                font-weight: 400;
                line-height: 124%;
                /* 35.96px */
                letter-spacing: -0.58px;
                background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;

                @media(max-width: 768px) {
                    font-size: 28px;
                    padding: 0 25px;
                }
            }
        }

        .features-availables_bottom {
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            background: #Fff;
            border-radius: 10px;
            background: #FFF;

            @media(max-width: 768px) {
                overflow-x: auto;
                -webkit-overflow-scrolling: touch;
                scrollbar-width: none;
                -ms-overflow-style: none;
                &::-webkit-scrollbar {
                    display: none;
                }
                justify-content: flex-start;
                margin-left: 50px;
            }

            .features-block_fixed {
                width: 20%;
                padding: 0 26px;
                flex-shrink: 0;

                @media(max-width: 768px) {
                    width: 137px;
                    flex: 0 0 137px;
                }

                .features-block_title {
                    color: #1E1C21;
                    font-family: Figtree, sans-serif;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                    text-transform: capitalize;
                }
            }

            .feature_available_list {
                width: 80%;
                display: flex;
                flex-shrink: 0;

                @media(max-width: 768px) {
                    width: max-content;
                }

                .feature_available_list_item {
                    width: 20%;
                    box-shadow: 0px 0px 3px 1px rgba(0,0,0,.09);
                    flex-shrink: 0;

                    @media(max-width: 768px) {
                        width: 137px;
                        flex: 0 0 137px;
                        padding: 0 10px;
                    }

                    .feature_available_list_item_icon {
                        height: 87px;
                        padding: 37px 20px 12px;
                        border-bottom: 1px solid #F6F7F7;
                    }

                    .feature_available_list_item_title {
                        text-align: center;
                        font-family: Poppins;
                        font-size: 15px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 124%;
                        /* 18.6px */
                        background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
                        background-clip: text;
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        width: 100%;
                        padding: 0;
                        margin-bottom: 0;
                    }

                    .feature_available_list_item_content {
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 106px;
                    }
                }
            }
        }
    }

    .features-availables_note {
        color: #8C8698;
        text-align: center;
        font-family: Figtree, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 124%;

        @media (max-width: 767px) {
            padding: 0 25px;
        }
        /* 19.84px */

        a {
            color: inherit;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 124%;
            text-decoration: none;
            border-bottom: 1px #6436bf solid;
            transition: all 0.3s ease;

            &:hover {
                color: #7646D8;
                text-decoration: none;
                transition: all 0.3s ease;
            }
        }
    }
}

