.single_post_wrapper {
  .incentive_cta {

    background: $purple;
    padding: 20px 33px;
    /* block-222 */
    box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);

    border-radius: 0px;
    margin-top: 25px;
    flex-direction: column;
    margin-bottom: 25px;

    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
      margin: 0px 0 15px;
      display: flex;
      padding: 0 70px;

      flex-direction: row;
      align-items: center;
      // padding: 28px;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
        0px 24px 60px rgba(6, 47, 125, 0.05),
        0px 12px 24px rgba(27, 59, 119, 0.05);
    }
    
    @media (min-width: $screen-lg) {
      padding: 0 calc((100vw - 1650px) / 2);
    }

    .icon {
      display: none;
      align-items: center;
      justify-content: center;

      @media (min-width: $screen-md) {
        display: flex;
        margin-top: 0;
        margin-right: 12px;
      }

      img {
        max-width: 208px;
        @media (min-width: $screen-md) {
          display: block;
          max-width: 106px;
        }
      }
    }

    .body {
      margin-bottom: 24px;
      @media (min-width: $screen-md) {
        margin-bottom: 0;
      }
    }

    .cta_title {
      @include pop(400, 23, 136%);
      color: #fff;
      letter-spacing: -0.46px;
      text-align: left;
      margin-bottom: 8px;
      @media (min-width: $screen-md) {
        margin-bottom: 4px;
      }
    }

    .cta_desc {
      @include fig(400, 15, 20px);
      color: #fff;
      text-align: left;
      @media (min-width: $screen-md) {
        font-size: 17px;
        line-height: 23px;
      }
    }

    .btn_group {
      align-items: center;
      justify-content: left;

      @media (min-width: $screen-md) {
        margin-left: auto;
        justify-content: center;
      }

      a {
        border-radius: 6px;
        border: 1.6px solid var(--Background-300, #BABFC9);
        padding: 18px 55px;
        color: #fff;
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
}
