.footer-holder {
  background: #eeeff2;
}


.footer {
  padding-top: 90px;
  overflow: visible;
  padding-bottom: 40px;

  .container {
    display: flex;
    flex-direction: column;
    padding: 0 30px;

    @media (min-width: $screen-md) {
      padding: 0 40px;
      flex-direction: row;
    }

    .footer-menus {
      display: flex;
      flex-direction: column;
      max-width: 100%;
      position: relative;
      z-index: 2;

      @media (min-width: $screen-md) {
        flex-direction: row;
        max-width: 90%;
        min-width: 90%;
      }
      .first-footer {
        .wp-block-social-links {
          display: none !important;
        }
        @media (min-width: $screen-md) {
          .wp-block-social-links {
            display: flex !important;
          }
        }
      }
      .fourth-footer {
        .wp-block-social-link {
          background: none;
          svg {
            font-size: 14px;
          }
        }
        @media (min-width: $screen-md) {
          .wp-block-social-links {
            display: none;
          }
        }
      }

      .footer-menu {
        flex-basis: 33%;
        margin-bottom: 48px;
        &:last-child {
          margin-bottom: 16px;
        }

        @media (min-width: $screen-md) {
          margin-bottom: 0;
        }

        .widget-title {
          font-family: "Poppins", sans-serif;
          font-weight: 400;
          font-size: 29px;
          line-height: 124%;
          letter-spacing: -0.02em;
          background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;
          margin-bottom: 32px;
        }

        ul.menu {
          display: flex;
          flex-direction: column;
          gap: 32px;

          li {
            a {
              font-family: "Figtree", sans-serif;
              font-style: normal;
              font-weight: 400;
              font-size: 20px;
              line-height: 156%;
              color: #5a6272;
              text-decoration: none;
              transition: 0.4s;
              @media (min-width: $screen-md) {
                font-size: 18px;
              }

              &:hover {
                color: #7a50ce;
              }
            }
          }
        }
      }
    }
    .wp-block-social-links {
      margin-top: 48px;
      display: flex;
      gap: 16px;
      .wp-block-social-link {
        a {
          color: #fff !important;
          width: auto;
          display: inline-block;
          background: #5A6272;
          border-radius: 50%;
        }
      }
    }

    .footer-large-image {
      position: relative;
      max-width: 10%;
      width: 100%;
      display: none;

      @media (min-width: $screen-md) {
        display: block;
      }

      img {
        position: absolute;
        left: -300px;
        top: -40px;
        max-height: 540px;
        width: unset;
      }
    }
  }
}

.bottom-footer {
  border-top: 1px solid #d1d5db;
  padding: 40px 0;

  @media (min-width: $screen-md) {
    margin-top: 50px;
    padding: 0;
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 30px;
    font-family: "Figtree", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 156%;
    color: #5a6272;
    a {
      color: inherit;
    }
    a {
      text-decoration: none;
      &:hover {
        color: $purple;
      }
    }

    @media (min-width: $screen-md) {
      padding: 0 40px;
      height: 144px;
      flex-direction: row;
      align-items: center;
    }
    .seperator {
      width: 1px;
      height: 15px;
      background: #8d95a5;
      display: none;

      @media (min-width: $screen-md) {
        display: block;
      }
    }

    .left-bottom-footer {
      display: flex;
      flex-direction: column;
      gap: 16px;
      align-items: flex-start;
      margin-bottom: 16px;

      @media (min-width: $screen-md) {
        margin-bottom: 0;
        flex-direction: row;
        align-items: center;
      }

      .footer-logo {
        max-width: 32px;
      }

      .copyright-logo {
        max-width: 167px;
      }
    }
    .right-bottom-footer {
      display: flex;
      gap: 16px;
      flex-direction: column;
      &__text {
        @include pop(400, 23, 1.36);
        display: block;
        @media (min-width: $screen-md) {
          display: inline;
          @include fig(400, 20, 1.56);
        }
      }
      p {
        margin-bottom: 48px;
        @media (min-width: $screen-md) {
          margin-bottom: 0;
        }
      }
      @media (min-width: $screen-md) {
        flex-direction: row;
        align-items: center;
      }
    }
  }
}
