.blog_header {
  padding: 120px 0 0;

  .title_area {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    @media (min-width: $screen-md) {
      flex-direction: row;
      align-items: center;
      margin-bottom: 40px;
    }

    .title {
      @include pop(400, 44, 125%);
      letter-spacing: -0.88px;
      @include titleGrad();
      margin-bottom: 40px;
      @media (min-width: $screen-md) {
        margin-bottom: 0;
        font-size: 53px;
      }
    }
  }

  .blog_search {
    @media (min-width: $screen-md) {
      margin-left: 30px;
      width: 100%;
      max-width: 324px;
    }
    .form_wrapper {
      display: flex;
      border-radius: 4px;
      border: 1px solid #8d95a5;
      padding: 9px 12px;

      .icon {
        width: 100%;
        max-width: 16px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 12px;

        svg {
          path {
            fill: #4f4b58;
          }
        }
      }

      input {
        margin: 0;
        padding: 0;
        border: none;
        outline: none;
        box-shadow: 0;
        @include fig(500, 20, 136%);
        color: #4f4b58;
      }
    }
  }

  .category_area {
    margin-bottom: 30px;
    @media (min-width: $screen-md) {
      margin-top: 20px;
    }
  }

  .category_tabs {
    display: flex;
    flex-wrap: wrap;

    li.active {
      order: -1;
      a {
        background: #6436bf;
        .title {
          color: #fff;
        }
      }
    }

    a {
      align-items: center;
      display: flex;
      padding: 13px 12px;
      border-radius: 5px;
      background: #fff;
      text-decoration: none;
      margin: 0 10px 10px 0;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05),
        0px 24px 60px 0px rgba(6, 47, 125, 0.05),
        0px 2px 4px 0px rgba(7, 4, 146, 0.1);
      @media (min-width: $screen-md) {
        padding: 20px;
      }
    }

    .title {
      @include fig(500, 17, 136%);
      color: #6436bf;
      @media (min-width: $screen-md) {
        font-size: 20px;
      }
    }

    .count {
      @include fig(600, 13, 136%);
      color: #5a6272;
      padding: 6px 8px;
      border-radius: 444px;
      background: #eeeff2;
      margin-left: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 21px;
      @media (min-width: $screen-md) {
        margin-left: 12px;
        height: 25px;
        min-width: 28px;
      }
    }
  }
}
