@import 'industry/chapters';
@import 'industry/other-industries';
.industry-page {
	padding-top: 108px;
	a {
		text-decoration: none;
		transition: .3s;
	}
	h2 {
		@include titleGrad();
	}
	img {
		max-width: 100%;
		height: auto;
	}
	.page-content {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		max-width: 1440px;
		padding: 0 10px;
		margin-left: auto;
		margin-right: auto;
		align-items: flex-start;
		margin-bottom: 40px;
		@media (min-width: $screen-sm) {
			padding: 0 30px;
			gap: 16px;
			flex-wrap: nowrap;
			margin-bottom: 80px;
		}
		.page-sidebar {
			width: 100%;
			display: none;
			&__inner {
				padding: 24px;
				border-radius: 16px;
				background: #fff;
				box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
				h3 {
					@include titleGrad();
					@include pop(500, 20, 1.36);
					padding-bottom: 10px;
					border-bottom: 1px #EEEFF2 solid;
					margin: 8px 8px 15px 8px;
				}
				h4 {
					color: #777184;
					@include pop(400, 18, 1.36);
					margin-bottom: 16px;
				}
				@media (min-width: $screen-sm) {
					display: block;
					width: 267px;
				}
			}
			@media (min-width: $screen-sm) {
				display: block;
				width: 267px;
			}
		}
		&__inner {
			width: 100%;
			border-radius: 16px;
			background: #fff;
			box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
			@media (min-width: $screen-sm) {
				width: calc(100% - 283px);
			}
			.industry-breadcrumbs {
				padding: 28px 30px;
				display: flex;
				align-items: center;
				@include fig(500, 15, 24px);
				color: $purple;
				a {
					display: flex;
					align-items: center;
					color: #635E6E;
					text-decoration: none;
					svg {
						margin-right: 4px;
						width: 24px;
					}
					&:hover {
						color: $purple;
					}
				}
				.separator {
					margin: 0 8px;
				}
			}
		}
	}
	.newsletter_bar {
		.submitted-message {
			@include pop(400, 23, 1.36);
			position: relative;
			color: #fff;
			text-align: center;
			p {
				position: relative;
				z-index: 2;
			}
			&:before {
				content: '';
				display: block;
				width: calc(100% + 12px);
				height: calc(100% + 12px);
				position: absolute;
				left: -6px;
				top: -6px;
				background: #321B5F;
			}
		}
	}
	.industry-content-wrapper {
		a {
			&:not(.btn, .button, .list-item__link, .acrobits_next_post__link){
				border-bottom: 2px $purple solid;
				color: inherit;
				transition: .3s;
				&:hover {
					color: $purple;
				}
			}	
		}
	}
}
