.blog_wrapper {
  padding-bottom: 50px;
  
  .title_section {
    display: flex;
    align-items: center;
    .title {
      @include pop(400, 35, 136%);
      letter-spacing: -0.7px;
      padding: 20px 0;
      @include titleGrad;
      @media (min-width: $screen-md) {
        font-size: 35px;
        line-height: 124%;
        letter-spacing: -0.7px;
      }
    }

    .filter {
      margin-left: auto;
    }
  }


  .blog_post_wrapper {
    display: flex;
    flex-wrap: wrap;
    @media (min-width: $screen-sm) {
      margin-left: -5px;
      margin-right: -5x;
    }
    @media (min-width: $screen-md) {
      margin-left: -10px;
      margin-right: -10px;
    }
  }

  .load_more_posts {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0 0px;
    @include pop(400, 18, 100%);
    color: #6436bf;
    @media (min-width: $screen-md) {
      padding-bottom: 80px;
    }

    &:hover {
      cursor: pointer;
    }

    .icon {
      width: 100%;
      max-width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 5px;

      svg {
        max-width: 14px;
        transform: rotate(90deg);
        path {
          fill: #6436bf;
        }
      }
    }
  }
}
